.Home {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.home_header {
  display: flex;
  justify-content: space-between;
  padding: 20px 30px;
  align-items: right;
}
.home_headerLeft {
  display: flex;
  align-items: center;
}
.home_headerLeft p {
  margin-left: 20px;
  font-size: 15px;
}

.home_headerRight {
  display: flex;
  align-items: center;
}
.home_headerRight p {
  margin-left: 20px;
  font-size: 15px;
}
.home_headerRight .MuiSvgIcon-root {
  margin-left: 20px;
}
.home_headerRight .MuiAvatar-root {
  margin-left: 20px;
}
.home_body {
  flex: 1;
  display: flex;
  margin-top: 0%;
  flex-direction: column;
}
.home_body > img {
  object-fit: contain;
  height: 90px;
}
